import { Component, OnInit } from '@angular/core';
import { FormGroup, FormBuilder, Validators, FormArray } from '@angular/forms';
import { MetaService } from 'src/app/shared/services/meta.service';
import { Router, ActivatedRoute } from '@angular/router';
import { TrafficagentService } from '../trafficagent.service';

@Component({
  selector: 'app-edit-trafficagent-master',
  templateUrl: './edit-trafficagent-master.component.html',
  styleUrls: ['./edit-trafficagent-master.component.scss']
})
export class EditTrafficagentMasterComponent implements OnInit {

  public tamForm: any;
  public process: string;
  loading = false;

  public tamDetails: any;

  constructor(
    private fb: FormBuilder,
    private metaService: MetaService,
    private tam: TrafficagentService,
    private router: Router,
    private aroute: ActivatedRoute
  ) { }

  ngOnInit() {

    this.tamDetails = this.aroute.snapshot.data['tam_details'];
    this.buildForm();
    this.getOptions();
  }
  public buildForm(): void {
    this.tamForm = this.fb.group(
      {
        'name': [this.tamDetails.name],
        'mobile1': [this.tamDetails.mobile1],
        'mobile2': [this.tamDetails.mobile2],
        'status': [this.tamDetails.status]
      }
    );
  }

  public saveTam() {
    this.process = 'saving';
    this.tam.updatTam(this.tamDetails['_id'], this.tamForm.value).subscribe(mrInfo => {
      this.process = 'done';
      this.router.navigate(['/traffic-agent/trafficagent-master']);
    }, error => { this.process = 'done'; console.log(error);
  });
  }

  private getOptions() {

  }

}


