import { Injectable } from '@angular/core';
import { Resolve, ActivatedRouteSnapshot } from '@angular/router';
import { TrafficagentService } from './trafficagent.service';

@Injectable()
export class TrafficagentmasterReslover implements Resolve<any> {
    constructor(private aps: TrafficagentService) { }

    resolve(route: ActivatedRouteSnapshot) {
        return this.aps.getTam(route.paramMap.get('id'));
    }
}
