import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';

import { TrafficAgentListComponent } from './traffic-agent-list.component';
import { VehicleMonitoringComponent } from './components/vehicle-monitoring/vehicle-monitoring.component';
import { VehicleMonitoringReportsComponent } from './components/vehicle-monitoring-reports/vehicle-monitoring-reports.component';
import { DashboardComponent } from './components/dashboard/dashboard.component';
import { VehicleMonitoringV2Component } from './components/vehicle-monitoring-v2/vehicle-monitoring-v2.component';

// trafficagent-master
import { AddTrafficagentMasterComponent } from './components/add-trafficagent-master/add-trafficagent-master.component';
import { ListTrafficagentMasterComponent } from './components/list-trafficagent-master/list-trafficagent-master.component';
import { EditTrafficagentMasterComponent } from './components/edit-trafficagent-master/edit-trafficagent-master.component';
import { TrafficagentmasterReslover } from './components/trafficagentmaster-reslover';

const routes: Routes = [
  {
    path: '',
    component: TrafficAgentListComponent,
    children: [
      { path: '', redirectTo: 'vehicle-monitoring', pathMatch: 'prefix' },
      {path: 'vehicle-monitoring', component: VehicleMonitoringV2Component},
      {path: 'vehicle-monitoring/reports', component: VehicleMonitoringReportsComponent},
      {path: 'dashboard', component: DashboardComponent},
      // Trafic Agent master
      {path: 'trafficagent-master', component: ListTrafficagentMasterComponent},
      {path: 'trafficagent-master/:id/edit', component: EditTrafficagentMasterComponent,  resolve: { tam_details: TrafficagentmasterReslover }},
      {path: 'add-trafficagent-master', component: AddTrafficagentMasterComponent},

    ]
  }
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})


export class TrafficAgentRoutingModule { }
