import { Component, OnInit } from '@angular/core';
import { FormGroup, FormBuilder, Validators, FormArray } from '@angular/forms';
import { MetaService } from 'src/app/shared/services/meta.service';
import { Router } from '@angular/router';
import { TrafficagentService } from '../trafficagent.service';

@Component({
  selector: 'app-add-trafficagent-master',
  templateUrl: './add-trafficagent-master.component.html',
  styleUrls: ['./add-trafficagent-master.component.scss']
})
export class AddTrafficagentMasterComponent implements OnInit {
  public tamForm: any;
  public process: string;
  usertype: any;
  loading = false;

  constructor(
    private fb: FormBuilder,
    private metaService: MetaService,
    private tam: TrafficagentService,
    private router: Router
  ) {
    this.usertype = this.metaService.getSiteFromStorage().role;
  }

  ngOnInit() {
    this.buildForm();
    this.getOptions();
  }
  public buildForm(): void {
    this.tamForm = this.fb.group(
      {
        'name': [''],
        'mobile1': [''],
        'mobile2': [''],
        'status': ['', [Validators.required]],
      }
    );
  }

  public save() {

    this.process = 'saving';
    this.tam.addTam(this.tamForm.value).subscribe(mrInfo => {
      this.process = 'done';
      this.router.navigate(['/traffic-agent/trafficagent-master']);
    }, error => { this.process = 'done'; console.log(error);
  });
  }

  private getOptions() {

  }
}
