import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { GetApiurl } from '../../../../parameters';
import { TrafficagentService } from '../trafficagent.service';
import { MetaService } from 'src/app/shared/services/meta.service';
declare var $: any;

@Component({
  selector: 'app-list-trafficagent-master',
  templateUrl: './list-trafficagent-master.component.html',
  styleUrls: ['./list-trafficagent-master.component.scss']
})
export class ListTrafficagentMasterComponent implements OnInit {

  public addButtonInfo: Object = { text: 'Add Traffic Agent Master', url: '/traffic-agent/add-trafficagent-master' };
  public fetchUrl: string = GetApiurl('traffic-agent/trafficagent-master');
  columns: Array<any> = [
  ];
  refreshRedTable: any;
  dataRow: any;
  usertype: any;
  process: string;
  public modal_title: string;
  public commonFilter: Object = {filterString: '', columnName: 'searchString', placeholder: 'Search by Name / Mobile no'};
  constructor(
    private router: Router,
    public meta: MetaService,
    private tas: TrafficagentService
  ) {
    this.usertype = this.meta.getSiteFromStorage().role;
    console.log(' Usertype >>>  ', this.usertype);

    if (this.usertype === 'ADMIN') {
      this.columns = [
        { title: 'Name', name: 'name', sort: true },
        { title: 'Mobile No 1', name: 'mobile1', sort: true },
        { title: 'Mobile No 2', name: 'mobile2', sort: true },
        { title: 'Status', name: 'status', sort: true },
        { title: 'Actions', links: ['Edit', 'Delete'] }
      ];
    } else {
      this.columns = [
        { title: 'Name', name: 'name', sort: true },
        { title: 'Mobile No 1', name: 'mobile1', sort: true },
        { title: 'Mobile No 2', name: 'mobile2', sort: true },
        { title: 'Status', name: 'status', sort: true },
        { title: 'Actions', links: ['Edit'] }
      ];
    }

  }

  ngOnInit() {
  }
  linkClicked(event: any): any {
    const data = event.data;
    this.dataRow = data;
    if (event.data.action === 'Edit') {
      this.router.navigate([`/traffic-agent/trafficagent-master/${this.dataRow['row']['_id']}/edit`]);
    }
    if (data.action === 'Delete') {
      $('#deleteModal').modal('show');
    }
  }
  public deleteRecord() {
    this.process = 'processing';
    this.deleteProject(this.dataRow['row']['_id']);
  }
  public deleteProject(index: number) {
    this.tas.deleteTam(index).subscribe(
      ng2TableData => {
        this.process = 'done';
        $('#deleteModal').modal('hide');
        this.refreshRedTable = { mode: 'delete', id: index };
      }, // filter(row => row.id != index),
      error => { console.log(error);
      }
    );
  }


}
